<script>
  import {Table,Icon,Layout} from 'src/'
  const columns = [{
    title: '',
    dataIndex: 'name',
    filters: [{
      text: '',
      value: '',
    }, {
      text: '',
      value: '',
    }],
    sorter:true,
    onFilter: (value, record) => record.name.indexOf(value) === 0
  }, {
    title: '',
    dataIndex: 'age',
    sorter: (a, b) => a.age - b.age,
    render(text, record,index) {
      return '<input type="text" v-model="gridData['+ index +'].age" />'
    }
  }, {
    title: '',
    dataIndex: 'address',
    filters: [{
      text: '',
      value: '',
    }, {
      text: '',
      value: '',
    }],
    filterMultiple: false,
    onFilter: (value, record) => record.address.indexOf(value) === 0
  },{
      title: '',
      key: 'operation',
      render(text, record) {
        if(record) {
          return '<icon type="info" /><a href="'+ record.key+'.html" target="_blank"></a>'
        }

      }
    }
  ];

  const data = [{
    key: '1',
    name: '',
    age: 32,
    address: '1',
  }, {
    key: '2',
    name: '',
    age: 42,
    address: '12',
  }, {
    key: '3',
    name: '',
    age: 32,
    address: '123',
  }, {
    key: '4',
    name: '',
    age: 32,
    address: '123',
  }];
  const rowSelection = {
    getCheckboxProps(record) {
      return {
        disabled: record.name === ''    // 
      };
    },
    onChange(selectedRowKeys, selectedRows) {
      console.log('rowSelection.onChange',selectedRowKeys, selectedRows);
    },
    onSelect(record, selected, selectedRows) {
      console.log('rowSelection.onSelect',record, selected, selectedRows);
    },
    onSelectAll(selected, selectedRows, changeRows) {
      console.log('rowSelection.onSelectAll',selected, selectedRows, changeRows);
    }
  };
  export default {
    components: {
      Grid:Table,
      Icon,
      Row:Layout.Row
    },
    data() {
      return {
        loading:false,
        gridData:data,
        gridColumns: columns,
        rowSelection:rowSelection
      }
    },
    methods:{
      changeData() {
        this.gridData.push({
          key: Math.random(),
          name: '',
          age: Math.random(),
          address: '123',
        })
      },
      emptyData() {
        this.gridData = []
      },
      onTableChange(i,j,k) {
        console.log('sdfsdfsdfsdf',i,j,k)
      },
      changeLoading() {
        this.loading = !this.loading
      }
    }
  }
</script>



